.book_container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  flex-direction: column;
}

.book {
  border: 1px solid red;
  padding: 20px;
  width: 90%;
  height: 100%;
  background-color: #171414;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
}

 
.title {
  font-size: 90px;
  color: white;
  font-family: monospace;
  text-align: center;
  overflow: auto;

}

.book_content {
  font-size: 10px;
  color: white;
  text-align: left;
  font-family: monospace;
}
.logo {
  width: 450px;
  height: 200px;
}
.img_container{
  display: flex;
  justify-content: center;
  align-items: center;

}  

.code_background {
background-color:#171414;
padding: 10px; 
font-family: monospace;
white-space: pre-wrap;
font-size: 10px;
color: greenyellow;
}


.code_background_pink {
  background-color:#171414;
  padding: 10px; 
  font-family: monospace;
  white-space: pre-wrap;
  font-size: 10px;
  color: #fa7ea8;
  }